.mailingModal {
    // display: none;
    position: fixed;
    z-index: 99;
    background-color: rgba(22, 21, 21, 0.7);
    width: 100%;
    height: 100%;
}

#mailingModal__sent {
    position: fixed;
    z-index: 100;
    left: 50%;
    margin-left: -300px;
    top: 50%;
    margin-top: -300px;
    border-radius: 10px;
    width: 600px;
    height: 250px;
    background-color: white;
    -webkit-box-shadow: 0px 0px 20px #c7c6c6;
    box-shadow: 0px 0px 20px #c7c6c6;

    p {
        text-align: center;

        &:first-child {
            color: rgb(130, 130, 216);
            font-size: 20px;
            font-weight: bold;
            padding-top: 20px;
        }

        &:nth-child(2) {
            color: rgb(69, 69, 71);
            font-size: 18px;
        }
    }

    form {
        display: flex;

        input[type="email"] {
            width: 350px;
            height: 47px;
            margin-left: 40px;
            border: 1px solid rgb(209, 208, 208);
            border-radius: 5px;
        }

        input[placeholder] {
            font-size: 18px;
        }

        .mailingButton {
            width: 150px;
            height: 50px;
            margin-left: 10px;
            background-color: rgb(255, 130, 130);
            border: none;
            border-radius: 5px;
            color: #fff;
            text-transform: uppercase;
            font-weight: bold;
            cursor: pointer;
            outline: none;

            &:hover {
                background-color: rgb(209, 77, 77);
            }

        }
    }
}